
const fold = window.innerWidth < 0 ? 'phone' : 'desc';

function openTask(id) {
    let xhr = new XMLHttpRequest();


    xhr.open("GET", `tasks/${fold}/${id}.svg`, false);
    xhr.overrideMimeType("image/svg+xml");
    xhr.onload = function (e) {
        try {
            const wrapper = document.createElement('div');
            wrapper.classList.add('task_wrapper');

            const modal = document.createElement('div');
            modal.classList.add('task_modal');


            modal.appendChild(xhr.responseXML.documentElement);
            wrapper.appendChild(modal);
            document.body.appendChild(wrapper);



            function close(e) {
                if (e.target !== e.currentTarget) return;
                wrapper.removeEventListener('click', close);
                wrapper.remove();
            }
            addClose(wrapper, close)
            wrapper.addEventListener('click', close);

            Array.from(wrapper.querySelectorAll('[data-type="tuskLink"]'))
                .forEach((el) => {
                    el.addEventListener('click', () => {
                        window.open('/')
                    })
                })
        } catch (e) {
            console.log(e)
        }
    };
    xhr.send("");
}



function addClose(el, handleClose) {
    const close = document.createElement('div');
    close.classList.add('close')
    close.addEventListener('click', handleClose)
    el.appendChild(close)

}
window.addEventListener('load', () => {

    let xhr = new XMLHttpRequest();
    xhr.open("GET", `tasks/${fold}.svg`, false);
    xhr.overrideMimeType("image/svg+xml");
    xhr.onload = function (e) {
        try {
            document.querySelector('.full').appendChild(xhr.responseXML.documentElement);

            const currentDate = (new Date()).getDate();
            const $dayEl = document.getElementById('curentDay');

            $dayEl.innerHTML = currentDate;


            const windows = Array.from(document.querySelectorAll('[data-window]'))
                .map((el) => {
                    return {
                        el: el,
                        day: el.getAttribute('data-window')
                    }
                });


            windows.forEach(({el, day}) => {
                const dayAsNum = parseInt(day);
                if (dayAsNum < 100) {
                    el.setAttribute("fill", '#AB2083')
                    el.addEventListener('click', () => {
                        openTask(day)
                    })
                } else {
                    el.style.cursor = 'default'
                }
            })

            Array.from(document.querySelectorAll("[data-answer]")).forEach(el => {
                el.addEventListener('click', () => {
                    let xhr = new XMLHttpRequest();
                    xhr.open("GET", `tasks/answer/${el.getAttribute('data-answer')}.svg`, false);
                    xhr.overrideMimeType("image/svg+xml");
                    xhr.onload = function (e) {
                        try {
                            const wrapper = document.createElement('div')
                            wrapper.classList.add('wrapper_answer');

                            wrapper.appendChild(xhr.responseXML.documentElement);

                            function close() {
                                wrapper.removeEventListener('click', close);
                                wrapper.remove();
                            }

                            wrapper.addEventListener('click', close);
                            document.body.appendChild(wrapper);
                            addClose(wrapper, close);
                        } catch (e) {
                            console.log(e)
                        }
                    };
                    xhr.send("");

                })
            })


            try {
                const gl = Array.from(document.querySelectorAll('[data-g]'));
                const gc = Array.from(new Set(gl.map((el) => el.getAttribute('fill'))))
                setInterval(() => {
                    gl.forEach((g) => {
                        g.setAttribute('fill', gc[Math.floor(Math.random() * gc.length)])
                    })
                }, 1500)

            } catch (e) {
                console.log(e)
            }

            try {
                function makeAnimation(el) {
                    const steeps = Math.random() + 5 + 3;
                    const transforms = Array.from({length: steeps}).fill(undefined).map((_, idx) => {
                    const height = window.innerHeight;


                        return {
                            "transform": `translateY(${height * (idx  / steeps) - height  / 5}px)`,
                            "opacity": 1 - (idx  / steeps)

                        }
                    })

                    const newspaperTiming = {
                        duration: Math.random() * 10000 + 1000,
                        iterations: 1,
                    };
                    const animation = el.animate(transforms, newspaperTiming);
                    animation.addEventListener('finish', () => {
                        makeAnimation(el)
                    })
                }

                Array.from(document.querySelectorAll("[data-snow]")).forEach(makeAnimation);

            } catch (e) {
                console.log(e)
            }
        } catch (e) {
            console.log(e)
        }
    };
    xhr.send("");


})